// Imports
import React, {Component} from 'react';
import {Link} from 'react-router-dom';

class ProductCard extends Component {

	render() {

		// Helpers
		const getHead = () => {
			if (this.props.heading) {
				return (
					<h3>{this.props.heading}</h3>
				)
			}
		}

		const getTitle = () => {
			if (this.props.title) {
				return (
					<h4>{this.props.title}</h4>
				)
			}
		}
		const getText = () => {
			if (this.props.text) {
				return (
					<p>{this.props.text}</p>
				)
			}
		}

		return (
			<div className={"col"}>
				<div className={"cta cta-product"}>
					{getHead()}
					{getTitle()}
					{getText()}
					<ul>
						<li>List item of features of this product</li>
						<li>List item of features of this product</li>
						<li>List item of features of this product</li>
					</ul>
					<Link to={this.props.link} className={"button"} title="Learn More">Learn More</Link>
				</div>
			</div>
		);

	}

}
export default ProductCard;