// Imports
import React, {Component} from 'react';
import {Link} from 'react-router-dom';

class CallToAction extends Component {

	render() {

		// Helpers
		const getTitle = () => {
			if (this.props.title) {
				return (
					<h4>{this.props.title}</h4>
				)
			}
		}
		const getText = () => {
			if (this.props.text) {
				return (
					<p>{this.props.text}</p>
				)
			}
		}

		if (this.props.type === "registerLarge") {
			return (
				<div className={"col"}>
					<div className={"cta cta-register register__lg"} style={{ backgroundImage: "url(" + this.props.image + ")" }}>
						<div className={"cta__content"}>
							{getTitle()}
							<Link to="/register/" className={"button btn-solid white"} title="Register Now">Register Now</Link>
							<Link to="/login/" className={"button white"} title="Login">Login</Link>
						</div>
					</div>
				</div>
			);
		} if (this.props.type === "registerMedium") {
			return (
				<div className={"col-md-8"}>
					<div className={"cta cta-register"} style={{ backgroundImage: "url(" + this.props.image + ")" }}>
						<div className={"cta__content"}>
							{getTitle()}
							<Link to="/register/" className={"button btn-solid white"} title="Register Now">Register Now</Link>
							<Link to="/login/" className={"button white"} title="Login">Login</Link>
						</div>
					</div>
				</div>
			);
		} if (this.props.type === "register") {
			return (
				<div className={"col"}>
					<div className={"cta cta-register"} style={{ backgroundImage: "url(" + this.props.image + ")" }}>
						<div className={"cta__content"}>
							{getTitle()}
							<Link to="/register/" className={"button btn-solid white"} title="Register Now">Register Now</Link>
							<Link to="/login/" className={"button white"} title="Login">Login</Link>
						</div>
					</div>
				</div>
			);
		} else if (this.props.type === "largeRight") {
			return (
				<div className={"col-md-10 cta cta-lg-right"}>
					<div className={"cta__content"}>
						{getTitle()}
						{getText()}
						<hr />
						<Link to={this.props.link} className={"button link"} title="Learn More">Learn More</Link>
					</div>
					<div className={"cta__img"} style={{backgroundImage: "url(" + this.props.image + ")" }} ></div>
				</div>
			);
		} else if (this.props.type === "large") {
			return (
				<div className={"col-md-10 cta cta-lg"}>
					<div className={"cta__content"}>
						{getTitle()}
						{getText()}
						<hr />
						<Link to={this.props.link} className={"button link"} title="Learn More">Learn More</Link>
					</div>
					<div className={"cta__img"} style={{backgroundImage: "url(" + this.props.image + ")" }} ></div>
				</div>
			);
		} else if (this.props.type === "medium") {
			return (
				<div className={"col-md"}>
					<div className={"cta cta-md"}>
						<img src={this.props.image} alt="alt-text" />
						{getTitle()}
						{getText()}
						<hr />
						<Link to={this.props.link} className={"button link"} title="Learn More">Learn More</Link>
					</div>
				</div>
			);
		} else {
			return (
				<div className={"col"}>
					<div className={"cta cta-sm"}>
						<img src={this.props.image} alt="alt-text" />
						{getTitle()}
						{getText()}
						<ul>
							<li>List item of features of this product</li>
							<li>List item of features of this product</li>
							<li>List item of features of this product</li>
						</ul>
						<Link to={this.props.link} className={"button"} title="Learn More">Learn More</Link>
					</div>
				</div>
			);
		}
	}

}
export default CallToAction;