import React, { Component } from 'react';
import Header from './global/Header';
import Footer from './global/Footer';
import PageBanner from './modules/_PageBanner';
import Accordion from './modules/_Accordion'
import { Link } from 'react-router-dom';

class PageTemplate extends Component {

	state = {
		path: this.props.location.pathname,
		bannerBg: '/images/bg-faq.jpg',
		bannerTitle: 'FAQ',
	};

	render() {
		return (
			<React.Fragment>

				<Header path={this.state.path} />

				<PageBanner type="interior" bg={this.state.bannerBg} title={this.state.bannerTitle} />

				<div id="main-content">
					<section className={"block"}>
						<div className={"container"}>
							<h3 className={"intro center"}>Wonder how this all works? Check out our frequently asked questions below to learn more about us. If you can’t find the answer you’re looking for, feel free to <Link to="/contact-us/" title="contact us">contact us.</Link></h3>
							<div className={"row justify-content-md-center"}>
								<Accordion />
							</div>
						</div>
					</section>
				</div>

				<Footer />

			</React.Fragment>
		);
	}
}
export default PageTemplate;