import React from 'react';
import { BrowserRouter, Route, Switch } from 'react-router-dom';

// Main pages
import AboutUs from './AboutUs';
import ContactUs from './ContactUs';
import Faq from './Faq';
import Home from './Home';
import News from './News';
import Privacy from './Privacy';
import ProductsServices from './ProductsServices';
import Terms from './Terms';
import VetWatch from './VetWatch';
import NotFound from './NotFound';

// Products and Services child pages
import Cvp from './products-services/Cvp';
import Compas from './products-services/Compas';

// News Post child page
import NewsPost from './news/NewsPost';

// External pages
import Login from './login/Login';
import Register from './login/Register';
import RegisterCode from './login/RegisterCode';
import RegisterForm from './login/RegisterForm';

// React Router
const Router = () => (
	<BrowserRouter>
		<Switch>
			<Route exact path="/" component={Home} />
			<Route exact path="/products-and-services/cvp" component={Cvp} />
			<Route exact path="/products-and-services/compas" component={Compas} />
			<Route exact path="/products-and-services/" component={ProductsServices} />
			<Route exact path="/vetwatch/" component={VetWatch} />
			<Route exact path="/faq/" component={Faq} />
			<Route exact path="/news/news-post" component={NewsPost} />
			<Route exact path="/news/" component={News} />
			<Route exact path="/about-us/" component={AboutUs} />
			<Route exact path="/privacy/" component={Privacy} />
			<Route exact path="/terms-of-use/" component={Terms} />
			<Route exact path="/contact-us/" component={ContactUs} />
			<Route exact path="/login/" component={Login} />
			<Route exact path="/register/" component={Register} />
			<Route exact path="/register-code/" component={RegisterCode} />
			<Route exact path="/register-form/" component={RegisterForm} />
			<Route component={NotFound} />
		</Switch>
	</BrowserRouter>
)
export default Router;