// Imports
import React, {Component} from 'react';

class CountryID extends Component {

	render() {
		return (

			<div className={"container-fluid"}>
				<div className={"row"}>
					<div className={"col"}>
						<div className={'country-id'}>
							<label htmlFor="country" className={"flag__ca"}></label>
							<select id="country">
								<option value="canada" defaultValue>Canada</option>
								<option value="usa">USA</option>
							</select>
						</div>
					</div>
				</div>
			</div>

		);
	 }
}
export default CountryID;