// Imports
import React, {Component} from 'react';
import {Link} from 'react-router-dom';

class PageBanner extends Component {

	render() {

		// Helpers
		const getTitle = () => {
			if (this.props.title) {
				return (
					<h1>{this.props.title}</h1>
				)
			}
		}
		const getText = () => {
			if (this.props.text) {
				return (
					<p className={"intro-text"}>{this.props.text}</p>
				)
			}
		}


		if (this.props.type === "home") {
			return (
				<div className={"page-banner banner__" + this.props.type} style={{backgroundImage: "url(" + this.props.bg + ")" }} >
					<div className={"container"}>
						<div className={"row"}>
							<div className={"col-sm-6"}>
								{getTitle()}
								{getText()}
								<Link to="/register/" className={"button btn-solid"} title="Register Now">Register Now</Link>
								<Link to="/login/" className={"button"} title="Log In">Log In</Link>
							</div>
						</div>
					</div>
				</div>
			);
		} else {
			return (
				<div className={"page-banner banner__" + this.props.type} style={{backgroundImage: "url(" + this.props.bg + ")" }} >
					<div className={"container"}>
						<div className={"row"}>
							<div className={"col-sm-6"}>
								{getTitle()}
								{getText()}
							</div>
						</div>
					</div>
				</div>
			);
		}
	}

}
export default PageBanner;