// Imports
import React, {Component} from 'react';

class MobileMenuBtn extends Component {

	render() {
		
		const mmenuToggle = () => {
			if (this.props.mmenuState) {
				return 'is-active';
			} else {
				return '';
			}
		}

	  	return (
	  		<button className={'hamburger hamburger--squeeze mobile-menu-btn ' + mmenuToggle()} type="button" 
	  			
	  			onClick={this.props.mmenuClick}
	  		>
	            <span className="hamburger-box">
	                <span className="hamburger-inner"></span>
	            </span>
	        </button>
	  	);
	}

}
export default MobileMenuBtn;